.meanings {
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
    scrollbar-width: thin;
    height: 55vh;
    border: 10px solid rgb(105, 105, 105);
    border-radius: 10px;
    padding: 10px 20px;
    overflow-x: hidden;
}

.subtitle {
    font-size: 5vw;
    font-family: 'Montserrat', sans-serif;
}

.singleMeanings {
    display: flex;
    flex-direction: column;
    border-radius: 10px;
    padding: 10px 20px;
    margin: 10px 0;
}

@media (max-width: 900px) {
    .meanings {
        height: 65vh;
        overflow-x: hidden;
        overflow-y: scroll;
    }
}